import Vue from "vue";
import Router from "vue-router";
import IndexPage from "@/components/IndexPage";
import Preview from "@/components/PreviewArticle";
Vue.use(Router);

export default new Router({
  routes: [
    {
      path: "/",
      name: "index",
      component: IndexPage,
    },
    {
      path: "/preview",
      name: "previewarticle",
      component: Preview,
    },
  ],
});



