            <input :placeholder="item.paramTips" v-model="item.paramValue" class="inputs" :style="{borderColor:formModel[1]&&formModel[1].elementButtonColor}"  v-if="item.paramField==='trackName'" maxlength="10" v-on:blur="changeNameFunForm(index,'trackName')">
            <input :placeholder="item.paramTips" v-model="item.paramValue" class="inputs" :style="{borderColor:formModel[1]&&formModel[1].elementButtonColor}"  v-if="item.paramField==='trackAddress'" maxlength="30" v-on:blur="changeNameFunForm(index,'trackAddress')">
            <input :placeholder="item.paramTips" v-model="item.paramValue" class="inputs" :style="{borderColor:formModel[1]&&formModel[1].elementButtonColor}" type="number"  v-if="item.paramField==='trackArea'"  maxlength="8"  oninput="value=value.replace(/^(\-)*(\d+)\.(\d\d).*$/,'$1$2.$3')" v-on:blur="changeNameFunForm(index,'trackArea')">
              <div class="messageTis"  v-if="item.message&&item.message!==''">{{item.message}}</div>
         </div>
          <Button class="btnsv" :style="{background:formModel[1]&&formModel[1].elementButtonColor,color: formModel[1]&&formModel[1].elementButtonTextColor}" v-on:click="handleAddForm()">
                {{formModel[1]&&formModel[1]&&formModel[1]&&formModel[1].elementButtonText}}
          </Button>
