<script>
window.alert = function(name){       
   var iframe = document.createElement("IFRAME");
    iframe.style.display="none";
    iframe.setAttribute("src", 'data:text/plain,');
    document.documentElement.appendChild(iframe);
    window.frames[0].window.alert(name);
    iframe.parentNode.removeChild(iframe);
};
import { baseurl } from "../services/config";
export default {
  name: 'HelloWorld',
  data () {
    return {
      data: {},
      imgList:[],
      foot:[],
      form:[],
      formModel:[],
      text:[],
      visible:false,
      tel:'',
      telShow:'',
      phone:'',
      scrollHeight:'',
      baseNum:133,
      istrue:false
    }
  },
   methods: {
    counter: function() {
    },
    handleFrom:function(){
      this.visible=true;
      let arr =document.getElementsByTagName("body")[0];
      arr.style.height="100%";
      arr.style.overflow="hidden";
    },
    handleFromHide:function(){
      this.visible=false;
      let arr =document.getElementsByTagName("body")[0];
      arr.style.height="auto"
      arr.style.overflow="auto";
    },
    changeNameFun:function(index,idx,name){
      const formv=this.form;
      let arr =this.form[index].paramList[idx];
      if(name==="trackPhone"){
        const reg = /^1([38]\d|5[0-35-9]|7[3678])\d{8}$/;
        if(arr.defaultValue===undefined||arr.defaultValue==='' ){
          this.form[index].paramList[idx].message='请输入联系电话';
        }else if(arr.defaultValue&&arr.defaultValue.length&&arr.defaultValue.length <= 10 || !reg.test(arr.defaultValue)) {
          this.form[index].paramList[idx].message='联系电话格式不正确'
        }else{
           this.form[index].paramList[idx].message=''
        }
      }else if(name==='trackName'){
         if(arr.paramRequired===1){
            if(arr.defaultValue===undefined||arr.defaultValue===''){
               this.form[index].paramList[idx].message='业主姓名不能为空'
            }else{
               this.form[index].paramList[idx].message=''
            }
          }
      }else if(name==='trackAddress'){
         if(arr.paramRequired===1){
            if(arr.defaultValue===undefined||arr.defaultValue===''){
               this.form[index].paramList[idx].message='楼盘/楼宇不能为空'
            }else{
               this.form[index].paramList[idx].message=''
            }
          }
      }else if(name==='trackArea'){
           if(arr.paramRequired===1){
            if(arr.defaultValue===undefined||arr.defaultValue===''){
                this.form[index].paramList[idx].message='建筑面积不能为空'
            }else if(arr.defaultValue&&arr.defaultValue.length&&arr.defaultValue.length>8){
               this.form[index].paramList[idx].message='面积不能大于99999.99'
          } else{
             this.form[index].paramList[idx].message=''
          }
         }
      }
      this.form=[...formv]
    },
    changeNameFunForm:function(idx,name){
      const formv=this.formModel;
      let arr =this.formModel[1].paramList[idx];
      console.log(arr)
      if(name==="trackPhone"){
        const reg = /^1([38]\d|5[0-35-9]|7[3678])\d{8}$/;
        if(arr.paramValue===undefined||arr.paramValue==='' ){
          this.formModel[1].paramList[idx].message='请输入联系电话';
        }else if(arr.paramValue&&arr.paramValue.length&&arr.paramValue.length <= 10 || !reg.test(arr.paramValue)) {
          this.formModel[1].paramList[idx].message='联系电话格式不正确'
        }else{
           this.formModel[1].paramList[idx].message=''
        }
      }else if(name==='trackName'){
         if(arr.paramRequired===1){
            if(arr.paramValue===undefined||arr.paramValue===''){
               this.formModel[1].paramList[idx].message='业主姓名不能为空'
            } else{
             this.formModel[1].paramList[idx].message=''
          }
          }
      }else if(name==='trackAddress'){
         if(arr.paramRequired===1){
            if(arr.paramValue===undefined||arr.paramValue===''){
               this.formModel[1].paramList[idx].message='楼盘/楼宇不能为空'
            }else{
             this.formModel[1].paramList[idx].message=''
          }
          }
      }else if(name==='trackArea'){
           if(arr.paramRequired===1){
            if(arr.paramValue===undefined||arr.paramValue===''){
                this.formModel[1].paramList[idx].message='建筑面积不能为空'
            }else if(arr.paramValue&&arr.paramValue.length&&arr.paramValue.length>8){
               this.formModel[1].paramList[idx].message='面积不能大于99999.99'
          }  else{
              this.formModel[1].paramList[idx].message=''
          }
         }
      }
      this.formModel=[...formv]
    },
    handleAdd:function(index,name){
      const formv=this.form;
      let arr =this.form[index].paramList;
      let data={};
      let isTrue=0;
      var query=this.$route.query;
      arr&&arr.map((item,idx)=>{
       if(item.paramField==='trackPhone'){
        const reg = /^1([38]\d|5[0-35-9]|7[3678])\d{8}$/;
        if(item.defaultValue===undefined||item.defaultValue==='' ){
          isTrue=1;
          this.form[index].paramList[idx].message='请输入联系电话';
          return false
        }else if(item.defaultValue&&item.defaultValue.length&&item.defaultValue.length <= 10 || !reg.test(item.defaultValue)) {
          this.form[index].paramList[idx].message='联系电话格式不正确'
          isTrue=1;
          return false
        }else{
           this.form[index].paramList[idx].message=''
        }
        data.trackPhone=item.defaultValue
       }
       if(item.paramField==='trackName'){
          if(item.paramRequired===1){
            if(item.defaultValue===undefined||item.defaultValue===''){
               this.form[index].paramList[idx].message='业主姓名不能为空'
               isTrue=1
              return false
            }
          }
          data.trackName=item.defaultValue
       }
        if(item.paramField==='trackAddress'){
          if(item.paramRequired===1){
            if(item.defaultValue===undefined||item.defaultValue===''){
               this.form[index].paramList[idx].message='楼盘/楼宇不能为空'
               isTrue=1
              return false
            }
          }
          data.trackAddress=item.defaultValue
       }
      if(item.paramField==='trackArea'){
         if(item.paramRequired===1){
            if(item.defaultValue===undefined||item.defaultValue===''){
                this.form[index].paramList[idx].message='建筑面积不能为空'
               isTrue=1
              return false
            }else if(item.defaultValue&&item.defaultValue.length&&item.defaultValue.length>8){
               this.form[index].paramList[idx].message='面积不能大于99999.99'
               isTrue=1
              return false
          } 
         }
          data.trackArea=item.defaultValue
       }    
      })
     this.form=[...formv]
      data.trackSource='TSC013';
      data.trackExternalUid=query.uid;
      data.trackInputType='0';
      if( isTrue===0){
     // alert(`公有专题，无法录入客户信息`);
       this.$axios({
          method: "POST",
          headers:{systemCode: 'S005'},
          url: baseurl + "/api/v1/wechat/track/www/push",
          data:data
        }).then(res => {
            if(res&&res.data.code==200){
                alert(`提交成功`);
                arr&&arr.map((item,idx)=>{
                  this.form[index].paramList[idx].defaultValue=''
                })
               this.form=[...formv]
            }else{
                alert(res.data.message);
            }
        })
      }
    },
   handleAddForm:function(){
      const formv=this.formModel;
      let arr =this.formModel[1].paramList;
      var query=this.$route.query;
      let data={};
      let isTrue=0;
      arr&&arr.map((item,idx)=>{
       if(item.paramField==='trackPhone'){
        const reg = /^1([38]\d|5[0-35-9]|7[3678])\d{8}$/;
        if(item.paramValue===undefined||item.paramValue==='' ){
          isTrue=1;
          this.formModel[1].paramList[idx].message='请输入联系电话';
