        }else if(item.paramValue&&item.paramValue.length&&item.paramValue.length <= 10 || !reg.test(item.paramValue)){
          this.formModel[1].paramList[idx].message='联系电话格式不正确';
          isTrue=1;
          return false
          }else{
            this.formModel[1].paramList[idx].message=''
          }
        data.trackPhone=item.paramValue
       }
       if(item.paramField==='trackName'){
          if(item.paramRequired===1){
            if(item.paramValue===undefined||item.paramValue===''){
               this.formModel[1].paramList[idx].message='业主姓名不能为空';
               isTrue=1
              return false
            }else{
             this.formModel[1].paramList[idx].message=''
            }
          }
          data.trackName=item.paramValue
       }
        if(item.paramField==='trackAddress'){
          if(item.paramRequired===1){
            if(item.paramValue===undefined||item.paramValue===''){
              this.formModel[1].paramList[idx].message='楼盘/楼宇不能为空';
               isTrue=1
              return false
            }else{
           this.formModel[1].paramList[idx].message=''
          }
          }
          data.trackAddress=item.paramValue
       }
      if(item.paramField==='trackArea'){
         if(item.paramRequired===1){
            if(item.paramValue===undefined||item.paramValue===''){
                this.formModel[1].paramList[idx].message='建筑面积不能为空';
               isTrue=1
              return false
            }else if(item.paramValue&&item.paramValue.length&&item.paramValue.length>8){
               this.formModel[1].paramList[idx].message='面积不能大于99999.99';
               isTrue=1
              return false
          } else{
             this.formModel[1].paramList[idx].message=''
            }
         }
          data.trackArea=item.paramValue
       }    
      })
      data.trackSource='TSC013'
      data.trackExternalUid=query.uid;
      data.trackInputType='0';
      this.formModel=[...formv]
      if( isTrue===0){
      //alert(`公有专题，无法录入客户信息`);
       this.$axios({
          method: "POST",
          headers:{systemCode: 'S005'},
          url: baseurl + "/api/v1/wechat/track/www/push",
          data:data
        }).then(res => {
            if(res&&res.data.code==200){
                alert(`提交成功`);
                 arr&&arr.map((item,idx)=>{
                   this.formModel[1].paramList[idx].paramValue='';
                })
               this.formModel=[...formv];
               this.handleFromHide();
            }else{
                alert(res.data.message);
            }
        })
      }
    },
  },
  created() {
      var query=this.$route.query;
      this.$axios({
          method: "POST",
          headers:{systemCode: 'S005'},
          url: baseurl + "/api/v1/wechat/special/www/getCollocation",
          // headers:{systemCode: 'S004'},
          // url: baseurl + "/api/v1/sso/special/h5/getCollocation",
          data:{specialUid:query.uid, terminalType: 1}
        }).then(res => {
          if(res&&res.data.code==200){
         document.title=res.data.data.specialTitle;
         let imgList=[];
         let foot=[];
         let form=[];
         let ph='';
         let text=[];
         let scrollHeight='';
         let baseNum=this.baseNum;
         res.data.data&&res.data.data.elementList&&res.data.data.elementList.map((item,index)=>{
             ph=item.companyPhone
            if(item.elementName==='图片广告'){
              scrollHeight=JSON.parse(item.elementStyle).scrollHeight
              imgList.push(item)
            }else if(item.elementName==='视图表单'){
              form.push(item)
            }else if(item.elementName==='常驻底部'){
              foot.push(item)
            }else if(item.elementName==='悬浮文本'){
              text.push(item)
            }
            })
            form&&form.map((item,index)=>{
              item.top=JSON.parse(item.elementStyle).top;
              item.left=JSON.parse(item.elementStyle).left;
            })
             text&&text.map((item,index)=>{
               let arr=JSON.parse(item.elementStyle)
               arr.top=arr.top/baseNum+'rem';
               arr.left=arr.left/baseNum+'rem';
               arr.width=arr.width/baseNum+'rem';
               arr.height=arr.height/baseNum+'rem';
               arr.fontSize=arr.fontSize/baseNum+'rem';
               text[index].elementStyle=JSON.stringify(arr)
            })
            this.scrollHeight=scrollHeight;
            this.imgList=imgList;
            this.foot=foot;
            this.form=form;
            this.phone=ph;
            this.text=text
            if(foot.length>0){
              this.$axios({
              method: "POST",
              headers:{systemCode: 'S005'},
              url: baseurl + "/api/v1/wechat/form/www/getCollocation",
              // headers:{systemCode: 'S004'},
              // url: baseurl + "/api/v1/sso/form/h5/getCollocation",
              data:{formUid:foot[0].formUid, terminalType: 1}
            }).then((newRes)=>{
               if(newRes&&newRes.data.code==200){
                 this.formModel=newRes.data.data.elementList
               }
            })
            }
           
          }else if(res&&res.data.code==2004){
            this.istrue=true
          }else{
              alert(res.data.message);
            }
        });
    }
}
</script>

