import { baseurl } from "./config";

//返回一个Promise(发送post请求)
export default function request(url, params, methods) {
  return new Promise((resolve, reject) => {
    console.log("====================================");
    console.log(url);
    console.log(params);
    console.log(baseurl);
    console.log("====================================");
    fetch(`${baseurl}/${url}`, {
      method: methods || "POST", //请求方法
      body: JSON.stringify({
        ...params
      }),
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json"
        // 'appId':
      }
    })
      .then(response => response.json())
      .then(data => {
        if (data && data.code === 200) {
          resolve(data);
        } else {
          reject(data);
        }
      })
      .catch(e => reject(e));
  });
}



