<template>
  <div class="previewWrap">
    <img :src="article.articleCoverImg" class="imgcover" />
    <p class="articletitle">{{ article.articleTitle }}</p>
    <p class="articledesc">
      <span>{{ article.createTime }}</span>
      <span>作者：{{ article.articleAuthor || "" }}</span>
      <span>{{ article.view || 0 }}</span>
    </p>
    <div class="preview">
      <div v-html="article.articleContent" class="articleCont"></div>
    </div>
    <p class="articlefotter">
      <span class="text">END</span>
      <span class="line"></span>
    </p>
  </div>
</template>

<script>
import request from "../services/request";
export default {
  name: "home",
  data() {
    return {
      article: { articleContent: null }
    };
  },
  created() {
    var query = this.$route.query;
    request("api/v1/wechat/article/applet/get", {
      articleUid: query.uid
    }).then(res => {
      console.log(res);
      if (res && res.code === 200) {
        this.article = res.data;
        // document.title = res.data.articleTitle;
        document.title = "详情";
      }
    });
  },
  methods: {}
};
</script>

<style scoped lang="scss">
h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  margin-bottom: 0.5em;
  color: rgba(0, 0, 0, 0.85);
  font-weight: 500;
}
.preview {
  margin: 0;
  padding: 12px;
  font-size: 16px;
  color: rgba(0, 0, 0, 0.65);
  text-align: left;
  p {
    min-height: 20px !important;
  }
  img,
  .image-wrap {
    max-width: 100% !important;
  }
}

.image-wrap,
.media-wrap {
  max-width: 100% !important;
}
img,
.image-wrap {
  max-width: 100% !important;
}
.articletitle {
  // height: 33px;
  font-size: 22px;
  font-family: PingFangSC-Medium, PingFang SC;
  font-weight: 530;
  color: #222222;
  line-height: 33px;
  padding: 10px 16px 0;
}
.articledesc {
  margin: 0 16px;
  // padding: 10px 0 16px;
  padding-bottom: 16px;
  padding-top: 10px;
  position: relative;
  height: 20px;
  line-height: 20px;
  border-bottom: 1px solid #e2e2e2;
  span {
    display: inline-block;
    font-size: 12px;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    color: #676767;
    padding-right: 16px;
  }
  span:nth-child(3) {
    position: absolute;
    right: 0px;
    bottom: 12px;
    padding-right: 0px;
    padding-left: 20px;
    background: url(../assets/ic_eye.png) no-repeat;
    background-size: 15px;
    background-position: 0 2px;
  }
}
.articlefotter {
  position: relative;
  height: 24px;
  margin: 16px;
  padding: 20px;
  margin-top: 40px;
  .text {
    font-size: 12px;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    color: #a1a1a1;
    padding: 0 10px;
    background: #fff;
    position: absolute;
    left: 45%;
    top: 4px;
    z-index: 1;
  }
  .line {
    position: absolute;
    left: 0;
    top: 12px;
    right: 0;
    border-bottom: 1px solid #e2e2e2;
    z-index: 0;
  }
}
</style>



